.pin{
  height: 5rem;
  width: 5rem;
  background-color: #FCFCFC;
  border: 5px solid #1B9AAA;
  border-radius: 50% 50% 0 50%;
  position: absolute;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  left: calc(-5rem / 2);
  top: calc(-5rem);
}

.pin:hover{
  height: 6rem;
  width: 6rem;
  border: 5px solid #033F63;
  left: calc(-6rem / 2);
  top: calc(-6rem);
}


