#mapContainer {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#mapContainer > div {
  max-width: 90%;
}

/* The map should be limited in width on larger screens */

@media (min-width: 768px) {
  #mapContainer > div {
    max-width: 70%;
  }
}



